body {
  text-align: center;
  background-color: rgb(255, 255, 255);
  font-family: "Poppins";
  font-style: italic;
  font-weight: 500;
}

.navbar {
  background: rgba(186, 186, 186, 0.504);
}

.logo {
  height: 70px;
  width: 10px;
}



.nav-link {
  list-style: none;
  font-size: 15px;
  cursor: pointer;
  color: black;
}

.nav-link:hover {
  border-bottom: 3px solid black;
  color: rgb(118, 118, 118);

}

.nav-link2 {
  text-decoration: none;
  list-style: none;
  font-size: 15px;
  cursor: pointer;
  color: black;
}

.sidebar-menu {
  list-style: none;
  font-size: 15px;
  cursor: pointer;
  color: rgb(32, 31, 31);
}

.sidebar-menu li {
  margin-top: 10px;
  font-weight: 500;
}

.card {
  text-align: left;
  width: 15rem;
  margin: 20px 20px;
}

.heading {

  color: whitesmoke;
  text-align: left;
}

.form1 {
  left: 0;
  box-shadow: 1px solid rgb(172, 171, 171);
  background: rgba(255, 255, 255, 0.486);
  border-radius: 10px;
}

.home-img {
  background-image: url('./components/images/3.jpeg');
  height: 780px;
  background-size: cover;
  color: white;
}

h2 {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  color: #333;
}

img {
  transition: transform 0.3s ease;
}

img:hover {
  transform: scale(1.05);
}

.container {
  padding: 20px;
}

.btn-sm {
  border-radius: 10px 0px;
  font-weight: 500;
  background-color: black;
  color: whitesmoke;
  font-family: sans-serif;
}

.btn-sm:hover {
  background-color: rgb(186, 186, 186);
  color: whitesmoke;
}

.footer-menu {
  text-decoration: none;
  text-align: left;
}

.list-items {
  text-decoration: none;
  color: black;
  font-size: 15px;
}

.list-items:hover {
  color: rgb(255, 255, 255);
}

.list-items2 {
  font-size: 25px;
  margin: 0px 5px;
  color: black;
}

.list-items2:hover {
  color: gray;
  border-bottom: 2px solid black;
}

.contactUs {
  padding: 50px;
  background-image: url('./components/images/9.png');
  text-align: left;
}

.card {
  border: 1px solid #2c96dd;
  border-radius: 10px 0px;
  color: rgb(0, 0, 0);
}

#navbar a {
  text-decoration: none;
  color: whitesmoke;
}
h2 {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  color: #333;
}

.gallery {
  object-fit: cover;
  height: 250px;
}

.row {
  justify-content: center;
}

.services-container {
  padding: 2rem;
  background-color: #f9f9f9;
}

.services-header {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #333;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 1.5rem;
}

.service-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.service-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.service-content {
  padding: 1rem;
}

.service-content h3 {
  font-size: 1.5rem;
  color: #000000;
  margin-bottom: 0.5rem;
}

.service-content ul {
  list-style-type: none;
  text-align: left;
  padding-left: 1.5rem;
  color: #555;
}

.service-content ul li {
  margin-bottom: 0.5rem;
}


@media (max-width: 576px) {
  img {
    height: 200px;
  }
}

@media screen and (max-width: 768px) {
  .footer-menu {
    text-decoration: none;
    text-align: center;
  }
}